.app-header {
  background: #333;
  color: #eeeeee;
  padding: 15px 10px;
  margin-bottom: 20px;
}

.app-header h1 {
  margin: 0;
}

h3 {
  margin-top: 20px;
}

.loader {
  text-align: center;
}

.container {
  max-width: 940px;
  background: #fcfcfc;
}

.chart-container {
  margin: 0 auto;
}

.wallet-coin {
  border: 1px solid gray;
  margin-bottom: 5px;
  padding: 5px;
}

.coin-symbol {
  background-color: #c1c1a1;
  color: white;
  margin: -5px -5px 5px -5px;
  text-align: center;
}

.symbol-text {
  margin-top: -1em;
  font-size: 2em;
  line-height: 2em;
}

.coin-data {
  max-width: 500px;
  margin: 0 auto;
}

.Value-cmp {
  padding: 2px;
  margin: 5px 0 0;
}

.Value-cmp .label {
  font-weight: 300;
  font-size: 0.8em;
  margin: 0;
  min-height: 0;
  color: #696767;
}

.Value-cmp .value {
  width: 100%;
  height: 100%;
  text-align: center;
  font-weight: 300;
  line-height: 1em;
  margin: 0;
  clear: none;
  min-height: 0;
  letter-spacing: -0.05em;
  padding: 0;
  color: #696767;
}

.positive .value {
  color: green;
}

.negative .value {
  color: red;
}

.Value-cmp.big .value {
  font-size: 2.3em;
}



